'use client';

import React, { useState } from 'react';
import useSWR from 'swr';
import {
    UserPlus, Search, Star, Clock, BadgeCheck,
    Edit2, Gift, Banknote, Receipt, Settings,
    Phone, Calendar, X
} from 'lucide-react';

const fetcher = (url: string) => fetch(url).then(r => r.json());

// Create/Edit Member Modal
const MemberModal = ({ member, onClose, onSave }: { member?: any, onClose: () => void, onSave: () => void }) => {
    const isEdit = !!member;
    const [formData, setFormData] = useState({
        memberCode: member?.memberCode || `M${Date.now().toString().slice(-6)}`,
        fullName: member?.fullName || '',
        phone: member?.phone || '',
        tier: member?.tier || 'BRONZE'
    });
    const [isSubmitting, setIsSubmitting] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsSubmitting(true);

        try {
            const url = isEdit ? `/api/members/${member.id}` : '/api/members/create';
            const method = isEdit ? 'PATCH' : 'POST';

            const res = await fetch(url, {
                method,
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(formData)
            });

            if (!res.ok) throw new Error('Failed to save');
            onSave();
        } catch (error) {
            alert('Gagal menyimpan member');
            console.error(error);
        } finally {
            setIsSubmitting(false);
        }
    };

    return (
        <div className="fixed inset-0 bg-black/80 backdrop-blur-sm z-50 flex items-center justify-center p-4">
            <div className="bg-[#0f1a14] border border-[#1e3328] rounded-xl w-full max-w-md overflow-hidden">
                <div className="p-4 border-b border-[#1e3328] bg-[#16261d] flex justify-between items-center">
                    <h3 className="text-lg font-bold text-white">{isEdit ? 'Edit Member' : 'Tambah Member Baru'}</h3>
                    <button onClick={onClose} className="text-slate-400 hover:text-white"><X size={20} /></button>
                </div>

                <form onSubmit={handleSubmit} className="p-6 space-y-4">
                    <div>
                        <label className="text-xs text-slate-500 uppercase font-bold block mb-1">Kode Member</label>
                        <input
                            type="text"
                            required
                            value={formData.memberCode}
                            onChange={e => setFormData({ ...formData, memberCode: e.target.value })}
                            disabled={isEdit}
                            className="w-full bg-[#050a07] border border-[#1e3328] rounded p-2 text-white text-sm focus:border-[#22c55e] outline-none font-mono disabled:opacity-50"
                        />
                    </div>

                    <div>
                        <label className="text-xs text-slate-500 uppercase font-bold block mb-1">Nama Lengkap</label>
                        <input
                            type="text"
                            required
                            value={formData.fullName}
                            onChange={e => setFormData({ ...formData, fullName: e.target.value })}
                            className="w-full bg-[#050a07] border border-[#1e3328] rounded p-2 text-white text-sm focus:border-[#22c55e] outline-none"
                        />
                    </div>

                    <div>
                        <label className="text-xs text-slate-500 uppercase font-bold block mb-1">No. Telepon</label>
                        <input
                            type="tel"
                            required
                            value={formData.phone}
                            onChange={e => setFormData({ ...formData, phone: e.target.value })}
                            className="w-full bg-[#050a07] border border-[#1e3328] rounded p-2 text-white text-sm focus:border-[#22c55e] outline-none"
                        />
                    </div>

                    <div>
                        <label className="text-xs text-slate-500 uppercase font-bold block mb-1">Tier</label>
                        <select
                            value={formData.tier}
                            onChange={e => setFormData({ ...formData, tier: e.target.value })}
                            className="w-full bg-[#050a07] border border-[#1e3328] rounded p-2 text-white text-sm focus:border-[#22c55e] outline-none"
                        >
                            <option value="BRONZE">Bronze</option>
                            <option value="SILVER">Silver</option>
                            <option value="GOLD">Gold</option>
                        </select>
                    </div>

                    <button
                        type="submit"
                        disabled={isSubmitting}
                        className="w-full py-3 bg-[#22c55e] hover:bg-[#16a34a] text-black font-bold rounded-lg disabled:opacity-50"
                    >
                        {isSubmitting ? 'Menyimpan...' : (isEdit ? 'Simpan Perubahan' : 'Tambah Member')}
                    </button>
                </form>
            </div>
        </div>
    );
};

export default function MembersPage() {
    const [selectedId, setSelectedId] = useState<number | null>(null);
    const [activeTab, setActiveTab] = useState<'history' | 'rewards' | 'settings'>('history');
    const [search, setSearch] = useState('');
    const [showModal, setShowModal] = useState(false);
    const [editingMember, setEditingMember] = useState<any>(null);

    // Fetch Members
    const { data: membersData, mutate } = useSWR(
        `/api/members?q=${search}`,
        fetcher
    );

    const members = membersData?.data || [];
    const selectedMember = members.find((m: any) => m.id === selectedId) || members[0];

    React.useEffect(() => {
        if (!selectedId && members.length > 0) {
            setSelectedId(members[0].id);
        }
    }, [members, selectedId]);

    const getTierColor = (tier: string) => {
        switch (tier) {
            case 'GOLD': return 'text-[#eab308] bg-[#eab308]/20 border-[#eab308]/30';
            case 'SILVER': return 'text-slate-300 bg-slate-700 border-slate-600';
            case 'BRONZE': return 'text-[#f97316] bg-[#f97316]/20 border-[#f97316]/30';
            default: return 'text-slate-500 bg-slate-800 border-slate-700';
        }
    };

    return (
        <div className="flex flex-1 overflow-hidden relative h-full">
            {/* Sidebar */}
            <aside className="w-[400px] bg-[#0f1a14] border-r border-[#1e3328] flex flex-col shrink-0 z-10">
                <div className="p-4 border-b border-[#1e3328] space-y-4">
                    <div className="flex justify-between items-center">
                        <h3 className="font-bold text-lg text-white">Daftar Member</h3>
                        <button
                            onClick={() => { setEditingMember(null); setShowModal(true); }}
                            className="flex items-center gap-2 bg-[#22c55e] hover:bg-[#16a34a] text-black text-sm font-bold py-1.5 px-3 rounded transition-colors"
                        >
                            <UserPlus size={18} /> Baru
                        </button>
                    </div>
                    <div className="relative group">
                        <span className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-500 group-focus-within:text-[#22c55e]">
                            <Search size={20} />
                        </span>
                        <input
                            className="w-full bg-[#050a07] border border-[#1e3328] rounded py-2 pl-10 pr-4 text-sm text-white focus:ring-1 focus:ring-[#22c55e] focus:border-[#22c55e] placeholder:text-slate-600 outline-none"
                            placeholder="Cari nama, ID, atau no HP..."
                            type="text"
                            value={search}
                            onChange={(e) => setSearch(e.target.value)}
                        />
                    </div>
                    <div className="flex gap-2 overflow-x-auto no-scrollbar pb-1">
                        <button className="bg-[#22c55e]/20 border border-[#22c55e] text-[#22c55e] px-3 py-1 rounded text-xs font-medium whitespace-nowrap">Semua</button>
                        {['VIP Gold', 'Silver', 'Expired'].map(filter => (
                            <button key={filter} className="bg-[#050a07] border border-[#1e3328] text-slate-400 hover:text-white px-3 py-1 rounded text-xs font-medium whitespace-nowrap transition-colors">
                                {filter}
                            </button>
                        ))}
                    </div>
                </div>

                <div className="flex-1 overflow-y-auto p-3 space-y-2">
                    {members.map((member: any) => (
                        <div
                            key={member.id}
                            onClick={() => setSelectedId(member.id)}
                            className={`group flex gap-3 p-3 border rounded cursor-pointer relative overflow-hidden transition-all
                  ${selectedId === member.id
                                    ? 'bg-[#16261d] border-[#22c55e]/50'
                                    : 'bg-[#050a07] border-[#1e3328] hover:bg-[#16261d] hover:border-[#1e3328]/80'
                                }
                `}
                        >
                            {selectedId === member.id && <div className="absolute left-0 top-0 bottom-0 w-1 bg-[#22c55e]"></div>}
                            <div className="w-12 h-12 rounded bg-slate-800 shrink-0 flex items-center justify-center font-bold text-slate-500 border border-[#1e3328]">
                                {member.fullName.substring(0, 2).toUpperCase()}
                            </div>
                            <div className="flex-1 min-w-0">
                                <div className="flex justify-between items-start mb-1">
                                    <h4 className={`font-bold text-sm truncate ${selectedId === member.id ? 'text-white' : 'text-slate-300 group-hover:text-white'}`}>
                                        {member.fullName}
                                    </h4>
                                    <span className={`text-[10px] font-bold px-1.5 py-0.5 rounded border ${getTierColor(member.tier)}`}>
                                        {member.tier}
                                    </span>
                                </div>
                                <p className="text-xs text-slate-500 mb-2">ID: {member.memberCode}</p>
                                <div className="flex items-center gap-4 text-xs">
                                    <span className={`flex items-center gap-1 ${selectedId === member.id ? 'text-[#22c55e]' : 'text-slate-400'}`}>
                                        <Star size={14} /> {member.pointsBalance.toLocaleString()} Poin
                                    </span>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </aside>

            {/* Main Content */}
            {selectedMember ? (
                <main className="flex-1 flex flex-col h-full bg-[#050a07] overflow-hidden relative">
                    {/* Header Gradient */}
                    <div className="absolute top-0 left-0 right-0 h-48 bg-gradient-to-b from-[#0f1a14] to-[#050a07] z-0 border-b border-[#1e3328]"></div>

                    {/* Profile Header */}
                    <div className="relative z-10 px-8 pt-8 pb-6 flex items-end justify-between">
                        <div className="flex items-end gap-6">
                            <div className="size-32 rounded-lg border-4 border-[#050a07] bg-slate-800 flex items-center justify-center shadow-lg">
                                <span className="text-4xl font-bold text-slate-500">{selectedMember.fullName.substring(0, 2).toUpperCase()}</span>
                            </div>
                            <div className="pb-2">
                                <div className="flex items-center gap-3 mb-1">
                                    <h1 className="text-3xl font-bold text-white">{selectedMember.fullName}</h1>
                                    <BadgeCheck size={24} className="text-[#eab308]" />
                                </div>
                                <div className="flex items-center gap-4 text-sm text-slate-400 mb-2">
                                    <span>ID: <span className="text-white font-mono">{selectedMember.memberCode}</span></span>
                                    <span className="w-1 h-1 bg-slate-600 rounded-full"></span>
                                    <span>Bergabung: <span className="text-white">{new Date(selectedMember.joinDate).toLocaleDateString()}</span></span>
                                    <span className="w-1 h-1 bg-slate-600 rounded-full"></span>
                                    <span>No. HP: <span className="text-white">{selectedMember.phone}</span></span>
                                </div>
                                <div className="flex gap-2">
                                    <span className={`text-xs font-bold px-2 py-1 rounded border ${getTierColor(selectedMember.tier)}`}>
                                        {selectedMember.tier} MEMBER
                                    </span>
                                    {selectedMember.status === 'ACTIVE' && (
                                        <span className="bg-[#22c55e]/10 text-[#22c55e] text-xs font-bold px-2 py-1 rounded border border-[#22c55e]/20">AKTIF</span>
                                    )}
                                </div>
                            </div>
                        </div>

                        <div className="flex gap-3 pb-2">
                            <button
                                onClick={() => { setEditingMember(selectedMember); setShowModal(true); }}
                                className="flex items-center gap-2 bg-[#0f1a14] hover:bg-[#16261d] border border-[#1e3328] text-white px-4 py-2 rounded font-medium transition-colors"
                            >
                                <Edit2 size={20} /> Edit Profil
                            </button>
                            <button
                                className="flex items-center gap-2 bg-[#eab308] hover:bg-yellow-500 text-black px-4 py-2 rounded font-bold border border-yellow-600 transition-colors shadow-none"
                            >
                                <Gift size={20} /> Tukar Reward
                            </button>
                        </div>
                    </div>

                    {/* Stats Grid */}
                    <div className="relative z-10 px-8 mb-6">
                        <div className="grid grid-cols-4 gap-4">
                            <div className="bg-[#0f1a14] border border-[#1e3328] p-4 rounded flex flex-col">
                                <span className="text-slate-500 text-xs uppercase font-bold tracking-wider mb-1">Sisa Poin</span>
                                <div className="flex items-end justify-between">
                                    <span className="text-3xl font-bold text-[#22c55e]">{selectedMember.pointsBalance.toLocaleString()}</span>
                                    <Star className="text-[#22c55e]/30" size={32} />
                                </div>
                            </div>
                            <div className="bg-[#0f1a14] border border-[#1e3328] p-4 rounded flex flex-col">
                                <span className="text-slate-500 text-xs uppercase font-bold tracking-wider mb-1">Total Kunjungan</span>
                                <div className="flex items-end justify-between">
                                    <span className="text-3xl font-bold text-white">42</span>
                                    <span className="text-xs text-[#22c55e] font-medium mb-1">+2 minggu ini</span>
                                </div>
                            </div>
                            <div className="bg-[#0f1a14] border border-[#1e3328] p-4 rounded flex flex-col">
                                <span className="text-slate-500 text-xs uppercase font-bold tracking-wider mb-1">Total Belanja</span>
                                <div className="flex items-end justify-between">
                                    <span className="text-2xl font-bold text-white">Rp 8.4jt</span>
                                    <Banknote className="text-slate-700" size={32} />
                                </div>
                            </div>
                            <div className="bg-[#0f1a14] border border-[#1e3328] p-4 rounded flex flex-col relative overflow-hidden">
                                <span className="text-slate-500 text-xs uppercase font-bold tracking-wider mb-1">Status Tier</span>
                                <div className="relative z-10">
                                    <span className="text-lg font-bold text-[#eab308]">{selectedMember.tier}</span>
                                    <div className="w-full bg-slate-800 h-1.5 rounded-full mt-2 overflow-hidden">
                                        <div className="bg-[#eab308] h-full w-[75%]"></div>
                                    </div>
                                    <span className="text-[10px] text-slate-400 mt-1 block">1.5jt lagi ke Platinum</span>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/* Tabs */}
                    <div className="px-8 border-b border-[#1e3328] flex gap-8 mb-0">
                        {[
                            { id: 'history', label: 'Riwayat Transaksi', icon: Receipt },
                            { id: 'rewards', label: 'Katalog Reward', icon: Gift },
                            { id: 'settings', label: 'Pengaturan', icon: Settings },
                        ].map((tab) => (
                            <button
                                key={tab.id}
                                onClick={() => setActiveTab(tab.id as any)}
                                className={`pb-3 border-b-2 font-medium text-sm flex items-center gap-2 transition-colors
                  ${activeTab === tab.id
                                        ? 'border-[#22c55e] text-[#22c55e]'
                                        : 'border-transparent text-slate-400 hover:text-white'
                                    }
                `}
                            >
                                <tab.icon size={18} />
                                {tab.label}
                            </button>
                        ))}
                    </div>

                    {/* Tab Content */}
                    <div className="flex-1 overflow-y-auto p-8 bg-[#050a07]">
                        {activeTab === 'history' && (
                            <div className="rounded border border-[#1e3328] overflow-hidden bg-[#0f1a14]">
                                <div className="p-8 text-center text-slate-500">
                                    Tidak ada riwayat transaksi.
                                </div>
                            </div>
                        )}
                        {activeTab !== 'history' && (
                            <div className="flex flex-col items-center justify-center h-full text-slate-500 pb-20">
                                <Settings size={32} className="opacity-50 mb-4" />
                                <p>Fitur {activeTab === 'rewards' ? 'Katalog Reward' : 'Pengaturan'} belum tersedia.</p>
                            </div>
                        )}
                    </div>
                </main>
            ) : (
                <main className="flex-1 flex items-center justify-center text-slate-500">
                    Pilih member untuk melihat detail
                </main>
            )}

            {showModal && (
                <MemberModal
                    member={editingMember}
                    onClose={() => setShowModal(false)}
                    onSave={() => { setShowModal(false); mutate(); }}
                />
            )}
        </div>
    );
}
